import React, { Component } from 'react';
import './barrage.scss';
import emitter from './event';

class Barrage extends Component{

    constructor(props) {
        super(props);
        this.state = {
            top : 10,//é»˜è®¤å¼¹å¹•å‡ºç°ä½ç½®å‚æ•°
            moveTime : 10,//é»˜è®¤å¼¹å¹•ç§»åŠ¨é€Ÿåº¦å‚æ•°
        };
      }

    componentWillUnmount() {
        emitter.removeListener(this.eventEmitter);
    }

    componentDidMount() {

        this.eventEmitter = emitter.addListener('receiveData', data => {
            const {bulletText} = data
            let setBullets = () => {
                let bulletArr = [],
                    text = bulletText,
                    len = bulletText.length,
                    moveTime = this.state.moveTime,
                    top = this.state.top;
    
                for (let i = 0; i < len; i++) {
                    let item = {
                        text:text[i],
                        top: Math.floor(Math.random()*(top + 30)) + top + "px",
                        moveTime: Math.ceil(Math.random() * moveTime) + 5 +'s'
                    }
                    bulletArr.push(item);
                }
    
                let bullets = bulletArr.map((item, i)=> {
                    console.log(item)
                    return (
                        <div key={i} className="bullet" style={{"top" : item.top, "animationDuration": item.moveTime }} >
                            {item.text}
                        </div>
                        )
                    }
                )
    
                this.setState({ bullets })
            }
            setBullets()
        });
    }
    

    render(){
        return (
            <div className="barrage-wrap">
                {this.state.bullets || ''}
            </div>
        )
    }
}

export default Barrage