import React, { Component } from 'react';
import './fish.scss';
import emitter from './event';

class Fish extends Component{
    constructor(props) {
        super(props);
        this.state = { eating : false };
      }
    
    componentDidMount() {
        //åˆå§‹åŒ–æ¸¸åŠ¨é±¼
        let eating = this.state.eating || false,
            array = [1,2,3,4,5]
        let fishes = array.map((item,i)=>{
            let fishName = 'fish' + item
            return (
                <div key={i} className={fishName}></div>
            )
        }) 
        this.setState({ fishes })

        //ä¼ å€¼åƒé±¼é¥µï¼Œåˆ‡æ¢åŠ¨ç”»ç±»
        this.eventEmitter = emitter.addListener('fishEat', () => {
            if(!eating){
                let fishEatNum =  "fish-eat" + (Math.floor(Math.random() * 5) + 1)
                this.setState({ eating: true , fishEatNum })
                setTimeout(() => {
                    emitter.emit('hasFeedFish');
                    this.setState({ eating: false })
                }, 2500);
            }
        });
    }

    componentWillUnmount() {
        emitter.removeListener(this.eventEmitter);
    }
    
    render(){
        const { eating, fishes, fishEatNum } = this.state

        return(
            <div className="fishes-wrap" >
                <div className="bg-bubble"></div>
                <div className="turtle"></div>
                <div className="preloader-fish-eat"></div>
                { fishes }
                {  
                    eating 
                    ?
                        <div className={ fishEatNum }></div>
                    :   ""
                }
                <img src={""} alt="" />
            </div>
        )
    }
}

export default Fish
