import React, { Component } from 'react';
import './bubble.scss';
import emitter from './event';

class Barrage extends Component{

    constructor(props) {
        super(props);
        this.state = {
            bubbleArr:[],
            canClick: true
        };
    }
    componentWillUnmount() {
        emitter.removeListener(this.eventEmitter);
    }

    componentDidMount() {

        this.eventEmitter = emitter.addListener('receiveData', data => {
            
            const {bubbleData} = data;
            const {bubbleArr} = this.state;

            for (let i = 0; i < bubbleData.length; i++) {
                let item = {
                    text:bubbleData[i],
                    top: (Math.floor(Math.random()*500) + 440)/750*100 + "vw",
                    left: Math.floor(Math.random()*80) + "vw",
                    fly:false
                }
                bubbleArr.push(item);
            }
            this.setState(bubbleArr)
        });
    }

    clickBubble(index){
        const {bubbleArr, canClick} = this.state;
        if (!canClick) return
        bubbleArr[index]['fly'] = true
        this.setState({bubbleArr,canClick: false});
        emitter.emit('clickBubble',bubbleArr[index]);
        setTimeout(() => {
            this.setState({canClick: true});
        }, 1000);
    }


    render(){
        const {bubbleArr} = this.state;

        let bubbles = bubbleArr.map((item, i)=> {
             let duration = Math.floor(Math.random()* 7)+ 3 + 's'
            return (
                <div className="animated pulse slower infinite" style={{
                    "animationDuration" : duration
                }} key={i}>
                    <div  className={item.fly?"bubble fly-to-intimacy":"bubble"} style={{
                        "top":item.top,
                        "left":item.left
                    }} onClick={this.clickBubble.bind(this,i)}>+{item.text}</div>
                </div>
                )
            }
        )
        return (
            <div className="bubble-wrap">
                {bubbles || ''}
            </div>
        )
    }
}

export default Barrage